<template>
    <div style="width: 600px; margin: auto">
        <h1>consent forms</h1>

        <p-toolbar style="margin-bottom: 10px">
            <template #left>
                <p-button icon="pi pi-chevron-left" @click="goBack" />
            </template>
            <template #right v-if="loading">
                <i class="pi pi-spin pi-spinner" style="font-size: 1.5rem"></i>
            </template>
        </p-toolbar>

        <template v-if="menu">
            <p-toolbar
                v-for="(item, index) in menu.items"
                :key="index"
                style="margin-bottom: 10px; cursor: pointer"
                @click="handleMenuClick(item)"
            >
                <template #left>
                    <div style="font-size: 1.5rem">{{ item.label }}</div>
                </template>
            </p-toolbar>
        </template>
    </div>
</template>

<script>
import { onMounted, ref } from "vue-demi";
import axios from "axios";
export default {
    setup() {
        const fullMenu = ref([]);
        const menu = ref(null);
        const loading = ref(false);

        const getMenu = async () => {
            loading.value = true;
            try {
                const { data } = await axios.get(
                    "http://tablet.nlf.clinic:3280/nlforms/get_menu.php"
                );
                fullMenu.value = data;
                menu.value = data[0];
            } catch (err) {
            } finally {
                loading.value = false;
            }
        };

        const handleMenuClick = (item) => {
            if (item.subMenu == null) {
                getForm(item.formKey);
            } else {
                changeMneu(item.subMenu);
            }
        };

        const getForm = async (formKey) => {
            let url = "http://tablet.nlf.clinic:3280/nlforms/get_form_url.php";
            try {
                const { data } = await axios.get(url, {
                    params: {
                        demographic_no: 105337,
                        user_name: "Svetlana",
                        link_key: formKey
                    }
                });
                window.open(data.url, "_blank");
            } catch (error) {}
        };

        const changeMneu = (menuName) => {
            let index = fullMenu.value.findIndex(
                (item) => item.name == menuName
            );
            menu.value = fullMenu.value[index];
        };

        const goBack = () => {
            if (menu.value.parent) {
                let index = fullMenu.value.findIndex(
                    (item) => item.name == menu.value.parent
                );
                menu.value = fullMenu.value[index];
            }
        };

        onMounted(() => {
            getMenu();
        });

        return {
            menu,
            loading,
            handleMenuClick,
            goBack
        };
    }
};
</script>
